* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

li {
  display: flex;
  list-style: none;
  align-items: center;
  gap: 10px;
}
.country-info {
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-size: 18px;
  font-weight: 700;
  font-family: Roboto;
}
.country-info > p {
  margin-bottom: 0;
  margin-top: 0;
}
.country-info>img {
  width: 200;
}
.country-info > p > span {
  font-weight: 400;
  margin-left: 20px;
}